@import "../../global.scss";

.about {
    background-color: $secondaryColor;
    display: flex;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;
        display: none; /*for now. would need a workaround for maintaining 2 updated resumes 
                         i.e. one formatted for web, other for regular distribution */
          
        .imgContainer {
            position: relative;
            overflow: hidden;
            padding-top: 100%;
            margin-top: 3px;
            margin-left: 20px;
            width: 100%;
            padding-top: 100%;
            overflow: hidden;
            position: relative;

            @include mobile {
                justify-content: center;
                padding-top: 0%;
                position: absolute;
                overflow: hidden;
                margin-top: 3px;
                margin-left: 20px;
                width: 100%;
                padding-top: 100%;
                overflow: hidden;
                position: relative;
            }

            .responsive-iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100vw;
                height: 100vh;
                border: 0;
            }
        }
        
    }
    .right{
        flex: 0.5;
        position: relative;

        @include mobile {
            display: none;
        }

        .wrapper {
            width: 100%;
            height: 100%;
            margin-left: 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            p {
                font-size: 25px;
                font-weight: bold;
                color: $tertiaryColor;
                padding-right: 80px;
                
            }

            h1 {
                font-size: 50px;
                margin: 10px 0;
                color:  $mainColor;
            }
            h2 {
                font-size: 30px;
                color:  $mainColor;

            }
            h3 {
                font-size: 30px;
                color:  $mainColor;

            }
        }
        a {
            position: absolute;
            bottom: 20px;

            @include mobile {
                display: none;
            }

            img {
                width: 60px;
                height: 60px;
                animation: arrowBlink 1.5s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }
}